import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { TextField, Button, Container, Typography, Select, MenuItem } from '@mui/material';
import axios from 'axios';

// const API_URL = window.location.origin;

const API_URL = 'http://localhost:8080';
function Register() {
  const [nome, setNome] = useState('');
  const [email, setEmail] = useState('');
  const [senha, setSenha] = useState('');
  const [tipoDeUsuario, setTipoDeUsuario] = useState('ALUNO');
  const [cpf, setCpf] = useState('');
  const [dataNascimento, setDataNascimento] = useState(Date);
  const navigate = useNavigate();

  const handleRegister = async () => {
    try {
      await axios.post(`${API_URL}/auth/register`, { nome, email, senha, tipoDeUsuario, cpf, dataNascimento });
      alert('Usuário registrado com sucesso!');
      navigate('/');
    } catch (err) {
      alert('Falha ao registrar usuário ' + err);
    }
  };

  return (
    <Container maxWidth="sm">
      <Typography variant="h4" gutterBottom>Registrar</Typography>
      <TextField label="Nome" fullWidth margin="normal" value={nome} onChange={e => setNome(e.target.value)} />
      <TextField label="Email" type="email" fullWidth margin="normal" value={email} onChange={e => setEmail(e.target.value)} />
      <TextField label="Senha" type="password" fullWidth margin="normal" value={senha} onChange={e => setSenha(e.target.value)} />
      <TextField label="CPF" type="text" fullWidth margin="normal" value={cpf} onChange={e => setCpf(e.target.value)} />
      <TextField label="Data de Nascimento" type="date" fullWidth margin="normal" value={dataNascimento} onChange={e => setDataNascimento(e.target.value)} />
      {console.log(dataNascimento)}
      <Select fullWidth margin="normal" value={tipoDeUsuario} onChange={e => setTipoDeUsuario(e.target.value)}>
        <MenuItem value="ALUNO">Aluno</MenuItem>
        <MenuItem value="PROFESSOR">Professor</MenuItem>
        <MenuItem value="AQV">AQV</MenuItem>
      </Select>
      <Button variant="contained" color="primary" onClick={handleRegister}>Registrar</Button>
    </Container>

  );
}

export default Register;
