import { Client } from "@stomp/stompjs";
import SockJS from "sockjs-client";

// const WS_URL = `${window.location.protocol}//${window.location.hostname}:${window.location.port}/ws`;
const WS_URL = "http://localhost:8080/ws";
let stompClient = null;

export function connectWS(onMessage) {
  const token = sessionStorage.getItem("token");
  console.log("vai tentar conectar");
  const socket = new SockJS(`${WS_URL}?token=${token}`);
  stompClient = new Client({
    Authorization: `Bearer ${token}`, 
    webSocketFactory: () => socket,
    reconnectDelay: 5000,
    debug: (msg) => console.log(msg),
    onConnect: () => {
      console.log("Connected to WS");
      const username = sessionStorage.getItem("username");
      if (onMessage) onMessage(stompClient, username);
    },
    onStompError: (frame) => console.error("Broker error:", frame),
    onWebSocketError: (error) => console.error("WS error:", error),
  });
  stompClient.activate();
  console.log("conectou");
}

export { stompClient };
