import React, { useState, useEffect } from 'react';
import { TextField, Button, Container, Typography, Card, CardContent, Select, MenuItem } from '@mui/material';
import { connectWS, stompClient } from '../../services/websocket';
import axios from "axios";

// const API_URL = `${window.location.protocol}//${window.location.stname}:${window.location.port}`;
const API_URL = 'http://localhost:8080';

function Aqv() {
  const [pedidos, setPedidos] = useState([]);
  const aqvUsername = sessionStorage.getItem('username');

  useEffect(() => {
    // Primeiro, buscar as ocorrências existentes
    axios.get(`${API_URL}/ocorrencias`)
      .then(response => {
        const ocorrencias = response.data;

        // Para cada ocorrência, buscar o nome do aluno
        Promise.all(ocorrencias.map(ocorrencia =>
          axios.get(`${API_URL}/alunos/${ocorrencia.alunoId}/nome`)
            .then(res => ({
              ...ocorrencia,
              nomeAluno: res.data
            }))
        )).then(ocorrenciasComNomes => {
          setPedidos(ocorrenciasComNomes);

          // Agora conectar ao WebSocket
          connectWS((client) => {
            client.subscribe(`/user/queue/ocorrencia`, msg => {
              const novaOcorrencia = JSON.parse(msg.body);
              axios.get(`${API_URL}/alunos/${novaOcorrencia.alunoId}/nome`).then(response => {
                novaOcorrencia.nomeAluno = response.data;
                setPedidos(prev => [...prev, novaOcorrencia]);
              });
            });
          });
        });
      })
      .catch(error => {
        console.error("Erro ao buscar ocorrências:", error);
      });
  }, [aqvUsername]);

  const aprovar = (ocorrencia) => {
    if (stompClient) {
      ocorrencia.status = 'AGUARDANDO_CIENCIA_PROFESSOR';
      stompClient.publish({
        destination: '/app/ocorrencia/decisao',
        body: JSON.stringify(ocorrencia)
      });
    }
    setPedidos(prev => prev.filter(p => p.id !== ocorrencia.id));
    alert('Ocorrência aprovada e enviada ao Professor');
  };

  const reprovar = (ocorrencia) => {
    if (stompClient) {
      ocorrencia.status = 'REPROVADO';
      stompClient.publish({
        destination: '/app/ocorrencia/decisao',
        body: JSON.stringify(ocorrencia)
      });
    }
    setPedidos(prev => prev.filter(p => p.id !== ocorrencia.id));
    alert('Ocorrência reprovada');
  };

  return (
    <Container maxWidth="sm">
      <Typography variant="h5">AQV - Pedidos Pendentes</Typography>
      {pedidos.map((p, idx) => p.status === 'AGUARDANDO_AUTORIZACAO' && (
        <Card key={idx} sx={{ my: 2 }}>
          <CardContent>
            <Typography>Aluno: {p.nomeAluno} ID: {p.alunoId}</Typography>
            <Typography>Descrição: {p.descricao}</Typography>
            <Button variant="contained" color="primary" onClick={() => aprovar(p)}>Aprovar</Button>
            <Button variant="outlined" color="error" onClick={() => reprovar(p)} sx={{ ml: 2 }}>Reprovar</Button>
          </CardContent>
        </Card>
      ))}
    </Container>
  );
}

export default Aqv;
