import React, { useState, useEffect } from 'react';
import { TextField, Button, Container, Typography, Card, CardContent, Select, MenuItem } from '@mui/material';
import { connectWS, stompClient } from '../../services/websocket';
function Professor() {
  const [ocorrencias, setOcorrencias] = useState([]);
  const professorUsername = sessionStorage.getItem('username');
  
    useEffect(() => {
      connectWS((client) => {
        client.subscribe(`/user/queue/ocorrencias`, msg => {
          setOcorrencias(prev => [...prev, JSON.parse(msg.body)]);
        });
      });
    }, [professorUsername]);

    const darCiencia = (ocorrencia) => {
      if (stompClient) {
        stompClient.publish({
          destination: '/app/ocorrencias/ciencia',
          body: JSON.stringify(ocorrencia)
        });
        alert('Ciência confirmada e aluno notificado');
      }
    };
  
    return (
      <Container maxWidth="sm">
        <Typography variant="h5">Professor - Ocorrências Pendentes de Ciência</Typography>
        {ocorrencias.map((o, idx) => (
          <Card key={idx} sx={{ my: 2 }}>
            <CardContent>
              <Typography>Aluno : {o.nome}</Typography>
              <Typography>Descrição: {o.descricao}</Typography>
              <Button variant="contained" onClick={() => darCiencia(o)}>Dar Ciência</Button>
            </CardContent>
          </Card>
        ))}
      </Container>
    );
  }

export default Professor;