import React, { useState, useEffect } from "react";
import {
  Container,
  Typography,
  TextField,
  Button,
  Card,
  CardContent,
  Select,
  MenuItem,
} from "@mui/material";
import { connectWS, stompClient } from "../../services/websocket";
import axios from "axios";

// const API_URL = `${window.location.protocol}//${window.location.hostname}:${window.location.port}`;

const API_URL = "http://localhost:8080";

function Aluno() {
  const [resposta, setResposta] = useState(null);
  const [motivo, setMotivo] = useState("");
  const [unidadesCurriculares, setUnidadesCurriculares] = useState([]);
  const [unidadeCurricularSelecionadaId, setUnidadeCurricularSelecionadaId] = useState("");
  const [professorResponsavelId, setProfessorResponsavelId] = useState("");

  const alunoUsername = sessionStorage.getItem('username');
  const alunoId = sessionStorage.getItem("userId");

  useEffect(() => {
    connectWS((client) => {
      client.subscribe(`/user/queue/ocorrencia`, msg => {
        setResposta(JSON.parse(msg.body));
        console.log("resposta da ocorrencia", msg.body);
      });
    });

    axios.get(`${API_URL}/unidades-curriculares/${alunoId}/aluno`).then(response => {
      setUnidadesCurriculares(response.data);
      console.log("unidades curriculares", response.data);
    });
  }, [alunoUsername]);

  const handleUcChange = (e) => {
    const selectedUcId = e.target.value;
    setUnidadeCurricularSelecionadaId(selectedUcId);

    const ucSelecionada = unidadesCurriculares.find(uc => uc.id === selectedUcId);
    if (ucSelecionada && ucSelecionada.professorId) {
      setProfessorResponsavelId(ucSelecionada.professorId);
    } else {
      setProfessorResponsavelId("");
    }
  };

  const enviarPedido = () => {
    if (stompClient && stompClient.connected && professorResponsavelId) {
      const ocorrencia = {
        tipo: 'SAIDA_ANTECIPADA',
        unidadeCurricularId: unidadeCurricularSelecionadaId,
        professorResponsavelId,
        descricao: motivo,
        alunoId: parseInt(alunoId)
      };

      console.log("ocorrência enviada:", ocorrencia);

      stompClient.publish({
        destination: '/app/ocorrencia/saida',
        body: JSON.stringify(ocorrencia)
      });

      alert('Pedido de saída enviado');
    } else {
      alert('Por favor, selecione uma unidade curricular com professor.');
    }
  };

  return (
    <Container maxWidth="sm">
      <Typography variant="h5">Aluno - Pedido de Saída Antecipada</Typography>

      <TextField
        label="Motivo"
        fullWidth
        value={motivo}
        onChange={e => setMotivo(e.target.value)}
      />

      <Select
        fullWidth
        value={unidadeCurricularSelecionadaId}
        onChange={handleUcChange}
        displayEmpty
        sx={{ mt: 2 }}
      >
        <MenuItem value="">Selecione uma unidade curricular</MenuItem>
        {unidadesCurriculares.map(uc => (
          <MenuItem key={uc.id} value={uc.id}>{uc.nomeUnidadeCurricular} - ({uc.nomeProfessor})</MenuItem>
        ))}
      </Select>

      <Button variant="contained" onClick={enviarPedido} sx={{ mt: 2 }}>
        Enviar Pedido
      </Button>

      {resposta && (
        <Card sx={{ mt: 2 }}>
          <CardContent>
            <Typography>Status: {resposta.status}</Typography>
            <Typography>Descrição: {resposta.descricao}</Typography>
          </CardContent>
        </Card>
      )}
    </Container>
  );
}

export default Aluno;
