import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { TextField, Button, Container, Typography } from '@mui/material';
import axios from 'axios';
import { jwtDecode } from 'jwt-decode';

// const API_URL = window.location.origin;
const API_URL = 'http://localhost:8080';


function Login() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();

  sessionStorage.clear();
  const handleLogin = async () => {
    try {
      const response = await axios.post(`${API_URL}/auth/login`, { username, password });
      const token = response.data.token;

      sessionStorage.setItem('token', token);
      sessionStorage.setItem('username', username);

      const decoded = jwtDecode(token);
      sessionStorage.setItem('userId', decoded.userId);
      sessionStorage.setItem('role', decoded.tipoDeUsuario);
      console.log(decoded);
      console.log(decoded.tipoDeUsuario);
      if (decoded.tipoDeUsuario === 'AQV') navigate('/aqv');
      else if (decoded.tipoDeUsuario === 'PROFESSOR') navigate('/professor');
      else navigate('/aluno');
    } catch (err) {
      alert('Login falhou');
    }
  };

  return (
    <Container maxWidth="sm">
      <Typography variant="h4" gutterBottom>Login</Typography>
      <TextField label="Username" fullWidth margin="normal" value={username} onChange={e => setUsername(e.target.value)} />
      <TextField label="Password" type="password" fullWidth margin="normal" value={password} onChange={e => setPassword(e.target.value)} />
      <Button variant="contained" color="primary" onClick={handleLogin}>Login</Button>
      <Button variant="outlined" color="secondary" onClick={() => navigate('/register')} sx={{ mt: 2 }}>Registrar</Button>
    </Container>
  );
}

export default Login;
